2px;\n    background: none;\n    font-size: 1rem;\n    border: #000 2px solid;\n    color: #000;\n    border-radius: 5px;\n\n}\n\n.header-right button:hover {\n    border-color: #fafafa;\n    color: #fafafa;\n}\n\n.header img{\n    width: 30px;\n}\n\n.middle-container {\n    position: relative;\n    flex-grow: 1;\n    display: flex;\n    flex-direction: row;\n    overflow: hidden;\n}\n\n.sidebar {\n    position: absolute;\n    height: 100%;\n    width: 18rem;\n    background-color: #fafafa;\n    opacity: 0;\n    transform: translate(-100%);\n    transition: all 200ms ease-in-out;\n}\n\n.sidebar.active {\n    transform: translate(0);\n    opacity: 1;\n}\n\n.sidebar-lists {\n    margin: 1rem\n}\n\n.sidebar-lists li {\n    cursor: pointer;\n    list-style-type: none;\n    font-size: 1.3rem;\n    padding: .5rem;\n    margin-bottom: .5rem;\n}\n\n.sidebar-lists li:hover {\n    background-color: #fff;\n}\n\n#new-list-btn:hover {\n    background-color: rgba(255, 255, 255, 0);;\n}\n\n\n\n/* Main body task display */\n\n.main-body {\n    flex-grow: 1;\n    transition: all 200ms ease-in-out;\n    margin-left: 0rem;\n    overflow: auto;\n}\n\n.content-container {\n    display: flex;\n    justify-content: center;\n}\n\n.task-container {\n    width: clamp(250px, 80%, 650px);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    \n}\n\n.header-title-div {\n   align-self: flex-start;\n}\n\n.task-line {\n    width: 100%;\n    margin-bottom: 20px;\n}\n\n.task-line.dragging {\n    opacity: .5\n}\n\n.task-line-top {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: flex-start;\n    border-bottom: 1px solid #333;\n    margin-bottom: 5px;\n}\n\n.task-line-left,\n.task-line-right {\n    margin-left: 5px;\n}\n\n.task-line-left{\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n}\n\n.task-check {\n    margin-right: 10px;\n}\n\n.task-line-right {\n    display: flex;\n    justify-content: flex-start;\n    margin-bottom: 5px;\n}\n\n.task-line-right img {\n    cursor: pointer;\n    width: 1.3rem;\n    margin-right: 10px;\n}\n\n.task-line-bottom {\n    display: flex;\n    flex-direction: column;\n    height: 0;\n    overflow: hidden;\n    transition: all 200ms ease-in-out;\n    justify-content: flex-start;\n    border-radius: 5px;\n    padding: 5px;\n}\n\n.task-line-bottom.visible {\n    height: auto;\n    border: 1px solid #333;\n}\n\n.tl-btm-left, \n.tl-btm-right  {\n    width: 100%;\n    margin: 0 5px;\n}\n\n.tl-btm-left div, \n.tl-btm-right div {\n    margin-top: 5px;\n}\n\n.tl-btm-title {\n    font-weight: bold;\n}\n\n/* Input modal */\n\n.list-input-modal,\n.task-input-modal,\n.delete-list-modal,\n.delete-modal {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%) scale(0);\n    transition: all 200ms ease-in-out;\n    border: 1px solid black;\n    border-radius: .3rem;\n    background-color: #fff;\n    width: 350px;\n    max-width: 80%;\n    transform: translate(-50%, -50%) scale(1);\n    z-index: 9;\n}\n\n.modal-header {\n    padding: 10px 15px;\n    display: flex;\n    border-top-left-radius: .3rem;\n    border-top-right-radius: .3rem;\n    justify-content: space-between;\n    align-items: center;\n    border-bottom: 1px solid black;\n    background-color: #ADD8E6;\n}\n\n.modal-title {\n    font-size: 1.25rem;\n    font-weight: bold;\n}\n\n.modal-close-button {\n    cursor: pointer;\n    border: none;\n    outline: none;\n    background: none;\n    font-size: 1.25rem;\n    font-weight: bold;\n}\n\n.modal-body,\n.list-modal-body,\n.delete-modal-body,\n.delete-list-modal-body {\n    padding: 10px 15px;\n}\n\n.form-top {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    align-content: center;\n\n}\n\n.form-top input,\n.form-top textarea,\n.form-top select {\n    margin-bottom: 10px;\n    padding: 5px;\n    font-size: 1rem;\n}\n\n.form-top input[type='datetime-local