 \n    .low-priority::before {\n        content: 'Low priority';\n        background-color: #000;\n        color: #fff;\n        text-align: center;\n        border-radius: .25rem;\n        padding: 5px;\n        width: 70px;\n        opacity: 0;\n        transition: opacity 200ms ease-in-out;\n    \n        position: absolute;\n        z-index: 1;\n        left: -25px;\n        bottom: 110%;\n    }\n    \n    .low-priority:hover::before {\n        opacity: 1;\n    }\n    \n    \n    .medium-priority {\n        position: relative;\n    }\n    \n    .medium-priority::before {\n        content: 'Medium priority';\n        background-color: #000;\n        color: #fff;\n        text-align: center;\n        border-radius: .25rem;\n        padding: 5px;\n        width: 70px;\n        opacity: 0;\n        transition: opacity 200ms ease-in-out;\n    \n        position: absolute;\n        z-index: 1;\n        left: -25px;\n        bottom: 110%;\n    }\n    \n    .medium-priority:hover::before {\n        opacity: 1;\n    }\n    \n    \n    .high-priority {\n        position: relative;\n    }\n    \n    .high-priority::before {\n        content: 'High priority';\n        background-color: #000;\n        color: #fff;\n        text-align: center;\n        border-radius: .25rem;\n        padding: 5px;\n        width: 70px;\n        opacity: 0;\n        transition: opacity 200ms ease-in-out;\n    \n        position: absolute;\n        z-index: 1;\n        left: -25px;\n        bottom: 110%;\n    }\n    \n    .high-priority:hover::before {\n        opacity: 1;\n    }\n    \n    #task-delete-btn {\n        position: relative;\n    }\n    \n    #task-delete-btn::before {\n        content: 'Delete';\n        background-color: #000;\n        color: #fff;\n        text-align: center;\n        border-radius: .25rem;\n        padding: 5px;\n        width: 60px;\n        opacity: 0;\n        transition: opacity 200ms ease-in-out;\n    \n        position: absolute;\n        z-index: 1;\n   